import React, { PureComponent } from 'react';
import '@vkontakte/vkui/dist/vkui.css';
import '../css/App.css';
import Icon16Chevron from '@vkontakte/icons/dist/16/chevron';
import Link from "@vkontakte/vkui/dist/components/Link/Link";

export class CardElement extends PureComponent {

    render() {
        let el = <div className={ "cell" }>
                    <div className={ "left" }> <div className="text">{ this.props.text }</div> </div>
                    <div className={ "right" }>  <Icon16Chevron fill="#c4c8cc"/> </div>
                </div>;
        return (
            this.props.hasOwnProperty('link') ?
                <Link href={this.props.link} target="_blank">{el}</Link>
                :
                {el}
        )
    }

}